body {
    margin: 0;
}

/* only webkit support */
::-webkit-scrollbar {
    width: 7px;
}
::-webkit-scrollbar-track {
    background-color: #696969;
}
::-webkit-scrollbar-thumb {
    background-color: #363636;
    transition: 0.3s background-color;
}
::-webkit-scrollbar-thumb:hover {
    background-color: #242424;
}
